@import '../../sassConfig/colors';
@import '../../sassConfig/transition';
.users {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
  padding: 2rem;
}

.users ul {
  list-style: none;
  padding: 1rem;
}

.users li {
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

.uname {
  color: $primary-blue;
  margin: 0.5rem auto;
}

.uphone {
  margin: 0.5rem auto;
}

.show-more {
  margin-left: calc(50% - 75px);
  width: 150px;
  height: 40px;
  margin-top: 1rem;
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  background-color: $primary-blue;
  color: white;
  font-weight: 500;
  font-size: 1.1rem;
  @include transition;
  &:hover {
    border: 2px solid $primary-blue;
    background-color: white;
    color: $primary-blue;
  }
  &:disabled,
  &[disabled] {
    background-color: gray;
    color: black;
  }
}
